import { useState } from "react";
// import { useFileUpload } from "../../../../../hooks/authentication-api";
import { useFinanceAllotScholarshipExcel } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
// import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../Ui/Button/Button.module.css";
import { useTranslation } from "react-i18next";
import ExcelImport from "../../../../ExcelImport/ExcelImport";
import { scholarshipRequiredFormat } from "../../../../Constant/Excel/scholarship_excel";
import SelectInstituteUniversalBatch from "../../../SelectInstituteUniversalBatch/SelectInstituteUniversalBatch";
const ScholarshipMapping = ({ aid, instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [excel, setExcel] = useState(false);
  const [financeAllotScholarshipExcel] = useFinanceAllotScholarshipExcel();

  const [step, setStep] = useState("");
  const [selectBatch, setSelectBatch] = useState(null);

  const onExcelUpload = () => {
    setExcel((pre) => !pre);
  };

  const onGoStep = () => {
    setStep(1);
  };
  const onCloseBatch = () => {
    setSelectBatch("");
    setStep("");
  };

  const onSelectUniversalBatch = (dt) => {
    if (dt?._id) {
      setSelectBatch(dt);
      setExcel(true);
    }
  };

  const onImportFileInSystem = (list, count, otherData) => {
    if (aid && list?.length > 0 && count) {
      onExcelUpload();
      setDisabled(true);
      financeAllotScholarshipExcel({
        aid: aid,
        excelFile: {
          excel_file: list,
          excel_count: count,
          scholar_batch: selectBatch?._id,
          excel_sheet_name: otherData?.excel_sheet_name,
        },
      })
        .then(() => {
          setDisabled(false);
          setStep("");
          setSelectBatch("");
          setExcel(false);
        })
        .catch({});
    }
  };
  return (
    <>
      {/* <UploadExcelButton
        dynamicKey="scholashipAllottment"
        onExcelFileUpload={onExcelFileUpload}
        title="Scholarship Allottment Excel"
        label="Allottment Excel"
      /> */}

      <label className={style.upload_excel_file} onClick={onGoStep}>
        <img
          src={`${assestsUserFinanceUrl}/upload-xslx.svg`}
          alt="add student icon"
          title={"Scholarship Allottment Excel"}
        />
        <h6>{t("Allottment Excel")}</h6>
      </label>
      {disabled && <QvipleLoader />}
      {excel && (
        <ExcelImport
          onClose={onExcelUpload}
          onImportFileInSystem={onImportFileInSystem}
          requiredFormat={scholarshipRequiredFormat}
          whichImportAccess="SCHOLARSHIP"
        />
      )}
      {step === 1 ? (
        <SelectInstituteUniversalBatch
          onSelectStaff={onSelectUniversalBatch}
          onClose={onCloseBatch}
          selectedId={selectBatch?._id}
          instituteId={instituteId}
        />
      ) : null}
    </>
  );
};

export default ScholarshipMapping;
